import { Injectable } from '@angular/core';
import { Headers, Http, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class ApiService {
  private api: string = 'https://figurehighscore.appspot.com/api'


  constructor (private http: Http){
  }

  public GetFigureList() {
    return this.http.get(this.api + '/figures');
  }

  public GetFigureResults(id: string) {
    return this.http.get(this.api + '/figures/' + id);
  }

  public DeleteFigure(id: string) {
    return this.http.delete(this.api + '/figures/' + id);
  }

  public DeleteResult(id: string) {
    return this.http.delete(this.api + '/results/' + id);
  }

  public GetPlayerResults(token: string) {
    return this.http.get(this.api + '/players/' + token);
  }

  public CreateFigure(figure: Figure) {
    return this.http.post(this.api + '/figures/new', JSON.stringify({figureID: figure.figureID, distance: figure.distance}));
  }

  public AddResult(result: any) {
    return this.http.post(this.api + '/results/new', JSON.stringify({figureID: result.figureID, username: result.username, lapTime: result.lapTime, token: result.token}));
  }

}

export interface Figure {
  figureID: string,
  distance: number,
}



