import { Component } from '@angular/core';
import { ApiService } from '../api.service';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-home',
  styleUrls: ['./player.component.css'],
  templateUrl: './player.component.html',
})

export class PlayerComponent {
  playerName: string = "";
  routeSubscription: Subscription;
  results: Array< { resID: string, figureID: string, lapTime: number, username: string, token: string} > = [];

  constructor (private apiService: ApiService, private route: ActivatedRoute){
    this.routeSubscription = route.params.subscribe((params: any) => {
      this.playerName = params['token'];
      this.getResults(params['token']);
    });


  }

  getResults(token: string) {
     this.apiService.GetPlayerResults(token).subscribe((data:any) => {
      console.log(JSON.parse(data._body));
      this.changeRows(JSON.parse(data._body));
    })
  }

  deleteResult(id: string, i: number) {
    this.apiService.DeleteResult(id).subscribe((data:any) => {
      this.results.splice(i, 1);
    });
  }

  public changeRows(data: any) {
    this.results = [];
    for (let i = 0; i <= data.length - 1; i++) {
      this.results.push({ 'resID': data[i].ResID, 'figureID': data[i].figureID, 'lapTime': data[i].lapTime, 'username': data[i].username, 'token': data[i].token });
    }
  }

}



