import { Component } from '@angular/core';
import { ApiService } from '../api.service';

@Component({
  selector: 'app-home',
  styleUrls: ['./home.component.css'],
  templateUrl: './home.component.html',
})

export class HomeComponent {
  newFigure: {figureID: string, distance: number} = {'figureID': "", 'distance': 0 };
  figures: Array< { figureID: string, distance: number} > = [];

  constructor (private apiService: ApiService){
    this.getFigures();
  }


  getFigures() {
    this.apiService.GetFigureList().subscribe((data:any) => {
      this.changeRows(JSON.parse(data._body));
    })
  }

  createFigure() {
    this.newFigure.distance = Number(this.newFigure.distance);
    this.apiService.CreateFigure(this.newFigure).subscribe((data:any) => {
      console.log(JSON.parse(data._body));
      this.getFigures()
    });
  }

  changeRows(data: any) {
    this.figures = [];
    for (let i = 0; i <= data.length - 1; i++) {
      this.figures.push({ 'figureID': data[i].figureID, 'distance': data[i].distance});
    }
  }

}



