import { Component } from '@angular/core';
import { ApiService } from '../api.service';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-home',
  styleUrls: ['./figure.component.css'],
  templateUrl: './figure.component.html',
})

export class FigureComponent {
  newResult: {figureID: string, username: string, lapTime: number, token: string,} = {'figureID': "", 'username': "", 'lapTime': 0, 'token': "" };
  figureName: string = "";
  routeSubscription: Subscription;
  results: Array< { resID: string, figureID: string, lapTime: number, username: string, token: string} > = [];

  constructor (private apiService: ApiService, private route: ActivatedRoute){
    this.routeSubscription = route.params.subscribe((params: any) => {
      this.figureName = params['id'];
      this.getResults(params['id']);
    });

  }

  getResults(id: string) {
     this.apiService.GetFigureResults(id).subscribe((data:any) => {
      this.changeRows(JSON.parse(data._body));
    })
  }

  deleteResult(id: string, i: number) {
    console.log(id);
    this.apiService.DeleteResult(id).subscribe((data:any) => {
       console.log(data._body);
       this.results.splice(i, 1);
    });
  }

  addResult() {
    this.newResult.lapTime = Number(this.newResult.lapTime);
    this.newResult.figureID = this.figureName;
    console.log(this.newResult);
    this.apiService.AddResult(this.newResult).subscribe((data:any) => {
      console.log(JSON.parse(data._body));
      this.getResults(this.figureName);
    });
  }

  public changeRows(data: any) {
    this.results = [];
    if (data != null) {
      for (let i = 0; i <= data.length - 1; i++) {
        this.results.push({
          'resID': data[i].resID,
          'figureID': data[i].figureID,
          'lapTime': data[i].lapTime,
          'username': data[i].username,
          'token': data[i].token
        });
      }
    }
  }

}



